function generatePizzaId() {
    let random  =  Math.random().toString(36).substring(7);
    let id = random+'_pizza'
    return id
}

export default function pizzaCatalog(){

pizzas.map(obj=> obj['id']=generatePizzaId())

return pizzas
}
  


